.grid {
    position: relative;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-auto-rows: minmax(100px, auto);
    grid-gap: 10px;
    gap: 10px;
    padding: 10px;
    max-width: 1920px;
    margin: auto;
}

@media screen and (min-width: 1024px) {
    .grid {
        grid-template-columns: repeat(4, 1fr);
        grid-auto-rows: minmax(450px, auto);
        grid-gap: 20px;
        gap: 20px;
        padding: 20px;
    }
}
.header__photo {
