.switch {
    position: relative;
    display: inline-block;
    width: 56px;
    height: 28px;
  }
  
  /* Hide default HTML checkbox */
  .switch input {
    opacity: 0;
    width: 0;
    height: 0;
  }
  
  /* The slider */
  .slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: var(--color-secondary);
    border: 1px solid var(--color-primary);
    transition: .4s;
  }
  
  .slider:before {
    position: absolute;
    content: "";
    height: 20px;
    width: 20px;
    left: 4px;
    top: 50%;
    -webkit-transform: translate(0, -50%);
            transform: translate(0, -50%);
    background-color: var(--color-primary);
    transition: .4s;
    z-index: 10;
  }

  .slider:after {
    content: "";
    background-image: url(/static/media/sun.28dcf3f2.svg);
    width: 16px;
    height: 16px;
    display: block;
    position: absolute;
    right: 0;
    top: 50%;
    -webkit-transform: translate(-6px, -50%);
            transform: translate(-6px, -50%);
    transition: .4s;
    z-index: 5;
  }
  
  input:checked + .slider {
    background-color: var(--color-secondary);
    border: 1px solid var(--color-primary);
  }
  
  /* input:focus + .slider {
    box-shadow: 0 0 1px #2196F3;
  } */
  
  input:checked + .slider:before {
    -webkit-transform: translate(26px, -50%);
            transform: translate(26px, -50%);
    background-color: var(--color-primary);
  }

  input:checked + .slider:after {
    background-image: url(/static/media/moon.172113a3.svg);
    -webkit-transform: translate(-32px, -50%);
            transform: translate(-32px, -50%);
  }
  
  /* Rounded sliders */
  .slider.round {
    border-radius: 34px;
  }
  
  .slider.round:before {
    border-radius: 50%;
  }
